@import url(https://fonts.googleapis.com/css?family=Eczar&display=swap);
@import url(https://fonts.googleapis.com/css?family=Source+Code+Pro&display=swap);
@import url(https://fonts.googleapis.com/css?family=Kanit&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.projects-each {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 20px; 
    background-color: rgb(249, 249, 113, 0.8);
}
.projects-each > div {
    flex: 1 1;
}
.image-wrapper img {
    display: flex;
    height: 40vh;
    width: 100%;
    padding: 15px;
    margin-top: 20px;
}
.project-info {
    display: flex;
    flex-direction: column;
    width: 400px;
    height: auto;
    padding: 15px;
    font-size: 20px;
    font-family: 'Kanit', sans-serif;
    color: black;
}
.projects > div {
    flex: 0 1 50%;
    box-sizing:border-box;
    display: flex;
    justify-content: space-evenly;
}
.project-title{
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
    font-weight: bold;
    font-size: 30px;
    color: black;
}
.project-link a{
    color: black;
    text-decoration: none;
} 
.project-title:hover{
    color: rgb(45, 76, 175);
}
.project-code {
    display: flex;
    justify-content: center;
}
.project-code a:hover{
    color: rgb(45, 76, 175);
}
@media only screen and (max-width: 600px){
    .projects-each {
        width: 60vh; 
    }
    
}



html {
    scroll-behavior: smooth;
}
#project-container {
    display: flex;
    display: grid;
    grid-gap: 20px;
    grid-template-columns: 1fr 1fr;
    width: 10%;
    justify-items: center;
    align-content: center;
}
.projects {
    display: flex;
    justify-content: center;
    margin-top: 10%;
    margin-bottom: 10%;
    flex: 1 1;
}
body {
    background-position:center;
    background-attachment: fixed;
    background-image: radial-gradient(circle, #056c83, #075567, #073f4c, #052a33, #00171c);
}  
@media only screen and (max-width: 600px) {
    #project-container {
        display: flex;
        flex-direction: column;
    }
  }


.header {
    height: 700px;
    padding-top: 50px;
}
.title{
  position: relative;
  top: 50%;  
  width: 24em;
  color: white;
  margin: 0 auto;
  font-size: 180%;
  text-align: center;
  white-space: nowrap;
  overflow: hidden;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);    
  -webkit-animation: 1s ease-out 0s 1 slideInFromLeft;    
          animation: 1s ease-out 0s 1 slideInFromLeft;
  text-shadow: 2px 2px 4px #9b9595;
}

@-webkit-keyframes slideInFromLeft {
  0% {
      -webkit-transform: translateX(100%);
              transform: translateX(100%);
  }
}

@keyframes slideInFromLeft {
  0% {
      -webkit-transform: translateX(100%);
              transform: translateX(100%);
  }
}

.navbar {
    display: flex;
    justify-content: space-around;
    text-decoration: none;
    padding-top: 10vh;
}
a:link {
    text-decoration: none;
    color: white;
    font-weight: bold;
  }
.navbar a {
    color: white;
    padding: 15px;
    border-radius: 30px;
    font-size: 5vh;
  }
.navbar a:hover {
    color: greenyellow;
    padding: 15px;
  }

#footer {
    display: flex;
    height: 500px;
}
#title {
    display: flex;
    justify-content: center;
    color: white;
    font-size: 6vh;
    font-weight: 800;
    text-shadow: 2px 2px 4px #9b9595;
}
.contact-form{
    display: flex;
    flex-direction: column;
    padding: 50px;
    width: 100%;
    margin: 50px;
    margin-top: 20px
}

.contact-form input{
    font-size: 20px;
    margin-top: 10px;
    width: auto;
    height: 30px;
}

.contact-form textarea{
    font-size: 20px;
    margin-top: 20px;
    width: auto;
    height: 150px;
}
.zoom{
    display: flex;
    justify-content: center;
    -webkit-transition: -webkit-transform .2s;
    transition: -webkit-transform .2s;
    transition: transform .2s;
    transition: transform .2s, -webkit-transform .2s;
}
.zoom:hover { /* IE 9 */
    -webkit-transform: scale(1.2); /* Safari 3-8 */
    transform: scale(1.2); 
}
.self-portrait {
    margin: 30px;
    border:white 10px solid;
    border-radius: 50%;
}
img { 
    width: auto;
    height: 300px; 
} 
.contact-links a{
    color: white;
    padding: 8px;
    border-radius: 30px;
} 
.contact-links a:hover{
    color: greenyellow;
    padding: 8px;
    border-radius: 30px;
} 
.contact-links {
    margin: 1px auto;
    font-size: 4vh;
    display: flex;
    justify-content: space-evenly;
    flex-direction: column;
    text-shadow: 2px 2px 4px #9b9595;
}
@media only screen and (max-width: 600px){
    body {
      width: 920px;
    }
    img {
        height: 170px;
    }
    .contact-form{
        display: flex;
        flex-direction: column;
        width: 80%;
    }
    .contact-links {
        margin-left: 100px;
    }
  }

#about-me {
    display: flex;
    flex-direction: column;
    background-color: rgb(249, 249, 113, 0.8);
    font-size: 20px;
    margin: 5%;
    font-family: 'Kanit', sans-serif;
    padding: 60px;
}
#about-me h3 {
    overflow: hidden;
    text-align: center;
    font-family: 'Source Code Pro', monospace;
    font-size: 30px;
    margin-bottom: 5px;
   }
h3:before,
h3:after {
    background-color: black;
    content: "";
    display: inline-block;
    height: 5px;
    position: relative;
    vertical-align: middle;
    width: 20%;
}
h3:before {
    right: 0.5em;
    margin-left: -50%;
}
h3:after {
    left: 0.5em;
    margin-right: -50%;
}

.about-me h3 {
    overflow: hidden;
    text-align: center;
    font-family: 'Source Code Pro', monospace;
    font-size: 40px;
    margin-bottom: 50px;
   }
h3:before,
h3:after {
    background-color: black;
    content: "";
    display: inline-block;
    height: 5px;
    position: relative;
    vertical-align: middle;
    width: 20%;
}
h3:before {
    right: 0.5em;
    margin-left: -50%;
}
h3:after {
    left: 0.5em;
    margin-right: -50%;
}

.about-me-images h3{
    font-family: 'Source Code Pro', monospace;
    font-size: 20px;
    margin: 30px;
    text-align: center;
}

.about-me-images img{
    height: 100px;
    width: auto;
    margin-top: 20px; 
    margin-left: 15px;  
}
